@font-face {
    font-family: Instagram;
    src: url(../../fonts/Grandista.ttf);
}

@font-face {
    font-family: NFS;
    src: url(../../fonts/NFS.ttf);
}

.background {
    background-image: url("../../media/images/bg.jpg");
    background-attachment: fixed;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    padding: 10% 0 2.5% 0;
}

.background-text,
.background-text {
    font-size: 64px;
    /* background: -webkit-linear-gradient(0deg, #282828 0%, #A2A2A2 200%); */
    background: -webkit-linear-gradient(0deg, #A13CC2 -50%, #9F1B7F 25%, #FF971D 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    /* text-shadow: 0px 20px 50px #FFFFFF; */
    margin: 0;
    padding: 0;
}

.background-text-instant-gram {
    font-family: Instagram;
}

.background-text-sport {
    font-family: NFS;
}

/* Mobile */

@media (max-width: 768px) {

    .background {
        background-image: none;
        text-align: center;
        padding: 20px;
    }

    .background-text {
        font-size: 42px;
    }

}