.image-slider {
    position: relative;
    height: 85vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin: 0px 0px 90px 0px; */
    /* border: 1px solid black; */
}

.slider-image {
    width: 100%;
    border-radius: 0.25em;
    box-shadow: 0px 0px 10px #A8A8A8;
}

.slider-caption {
    position: absolute;
    width: 100%;
}

.left-arrow,
.right-arrow {
    font-size: 32px;
    color: #282828;
    position: absolute;
    top: 50%;
    z-index: 10;
    cursor: pointer;
    user-select: none;
    transition: color 0.5s;
}

.left-arrow {
    left: 0px;
}

.right-arrow {
    right: 0px;
}

.left-arrow:hover,
.right-arrow:hover {
    color: #A8A8A8;
    transition: color 0.5s;
}

.left-arrow:active,
.right-arrow:active {
    transform: scale(0.85);
    transition: transform 0.25s;
}

.slide {
    opacity: 0.5;
    transition-duration: 1s ease;
}

.slide.active {
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.05);
}

/* Mobile */


@media (max-width: 768px) {

    .image-slider {
        height: 42.5vh;
        overflow-x: hidden;
    }

    .slider-image {
        border-radius: 0em;
        box-shadow: none;
    }

    .left-arrow,
    .right-arrow {
        top: 45%;
        color: #28282850;
    }

}